import React from "react";
import {CurrentUserContext} from '../contexts/CurrentUserContext';


function Card(props) {
    const { onCardDislike, onCardLike, card } = props;

    function handleClick() {
      props.onCardClick(card);
    }  

    const currentUser = React.useContext(CurrentUserContext)

    const isOwn = card.owner._id === currentUser._id;

    const cardDeleteButtonClassName = (
      `gallery__trash ${isOwn ? 'gallery__trash_visible' : ''}`
    );

    const isLiked = card.likes.some(i => i._id === currentUser._id);

    const cardLikeButtonClassName = (`gallery__like ${isLiked ? 'gallery__like_liked' : ''}`);
  
    return (
          <li className="gallery__item">
            <img
              className="gallery__img"
              src={card.link}
              alt={card.name}
              onClick={() => handleClick(card)}
            />
            <div className="gallery__caption">
              <h2 className="gallery__text">{card.name}</h2>
              <div className="gallery__likes-container">
                <button
                  aria-label=""
                  type="button"
                  className={cardLikeButtonClassName}
                  onClick={() => { if(!isLiked) {
                    onCardLike(card)
                  } else 
                  onCardDislike(card)
                }
                }
                
                  
                ></button>
                <span className="gallery__likes-counter">
                  {card.likes.length}
                </span>
              </div>
            </div>
            <button
              aria-label=""
              type="button"
              className={cardDeleteButtonClassName}
              onClick={props.onDelModalClick}
              

            ></button>
          </li>
    );
  }
  
  
  export default Card;