import { Link } from "react-router-dom";
import logo from "../images/logo.svg";

function Header(props) {
  console.log(props.headerEmail)

  return (
    <header className="header">
      <img className="header__logo" src={logo} id="logo" alt="Around US Logo" />
      <div className="header__container">
        <p className="header__email">{props.headerEmail}</p>
      <Link to={props.headerLink} onClick={props.onClick} className={props.headerLinkClass}>
          {props.headerLinkText}
        </Link>
        </div> 
    </header>
  );
}

export default Header;
