import { Link } from "react-router-dom";

function CredentialsForm({ onSubmit, ...props }) {
  return (

      <div className={`popup__container popup-${props.name}__container`}>
        <form
          className={`popup__form popup-${props.name}__form`}
          name={`form-${props.name}`}
          id={`form-${props.name}`}
          onSubmit={onSubmit}
        >
          <h2 className={props.titleClass}>{props.title}</h2>
          {props.children}
          <button
            aria-label=""
            id={props.name}
            type="submit"
            className={`popup__submit-btn popup-${props.buttonClass}`}
          >
            {props.buttonText}
          </button>
          <div className={`popup-${props.linkContainer}`}>
            <p className={`popup-${props.linkContainerTextClass}`}>{props.linkContainerText}</p>
            <Link to={props.linkTo} className={`popup-${props.linkClass}`}>
              {props.linkText}
            </Link>
          </div>
        </form>
      </div>

  );
}
export default CredentialsForm;
