import PopupWithForm from "./PopupWithForm";
import React, { useEffect } from "react";

function AddPlacePopup(props) {
  const { isOpen, onClose } = props;

  const [title, setTitle] = React.useState("");
  const [imageLink, setImageLink] = React.useState("");

  function handleTitleChange(e) {
    setTitle(e.target.value);
  }

  function handleImageLinkChange(e) {
    setImageLink(e.target.value);
  }

  useEffect(() => {
    setTitle('');
    setImageLink('');
}, [isOpen]);

  function handleAddPlaceSubmit(e) {
    e.preventDefault();

    props.onCardAdd({
      name: title,
      link: imageLink,
    });
  }

  return (
    <PopupWithForm
      name="add"
      title="New place"
      titleClass="popup__title"
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleAddPlaceSubmit}
      buttonClass="add__submit-btn"
      buttonText="Create"
    >

      <input
        type="text"
        name="name"
        id="title"
        value={title}
        onChange={handleTitleChange}
        className="popup__input popup-add__input"
        placeholder="Title"
        required
        minLength="1"
        maxLength="30"
      />
      <div className="errorContainer">
        <span id="title-error" className="popup__input-errorMessage"></span>
      </div>
      <input
        type="url"
        name="link"
        id="image-link"
        value={imageLink}
        onChange={handleImageLinkChange}
        className="popup__input popup-add__input"
        placeholder="Image link"
        required
      />
      <div className="errorContainer">
        <span
          id="image-link-error"
          className="popup__input-errorMessage"
        ></span>
      </div>

    </PopupWithForm>
  );
}

export default AddPlacePopup;
