function ImagePopup({ isOpen, onClose, ...props }) {
    const { card } = props;
  
    return (
      <>
        {card ? (
          <section
            className={`popup popup-image ${isOpen ? "popup_visible" : ""}`}
          >
            <figure className="popup-image__container">
              <button
                aria-label=""
                type="button"
                className="popup__close-btn popup-image__close-btn"
                onClick={onClose}
              ></button>
              <img
                className="popup-image__picture"
                src={card.link}
                alt={card.name}
              />
              <figcaption className="popup-image__caption">
                {card.name}
              </figcaption>
            </figure>
          </section>
        ) : (
          ``
        )}
      </>
    );
  }
  
  export default ImagePopup;
  