import React from "react";
import { withRouter } from "react-router-dom";

import CredentialsForm from "./CredentialsForm";
import Header from "./Header";
import InfoToolTip from "./InfoTooltip";

function Register(props) {
  const {registerFail, isOpen, onClose, onSubmit, onChange, values } = props;

  return (
    <>
      <Header headerLink={"signin"} headerLinkClass={"popup-register__login-header-link"} headerLinkText={"Log in"}/>
      <CredentialsForm
        name="register"
        titleClass="popup-register__title"
        title="Sign up"
        onSubmit={ onSubmit }
        buttonClass="register__submit-btn"
        buttonText= {"Sign up"}
        linkContainer={"register__signin"}
        linkContainerTextClass={"register__login-text"}
        linkContainerText={"Already a member?"}
        linkTo={"signin"}
        linkClass={"register__login-link"}
        linkText={"Log in here!"}
      >
        <input
          type="email"
          name="email"
          id="email"
          value={values.email}
          onChange={onChange}
          className="popup-register__input"
          placeholder="Email"
          required
          minLength="1"
          maxLength="30"
        />
        <div className="errorContainer">
          <span id="email-error" className="popup__input-errorMessage"></span>
        </div>
        <input
          type="password"
          name="password"
          id="password"
          value={values.password}
          onChange={onChange}
          className="popup-register__input"
          placeholder="Password"
          required
        />
        <div className="errorContainer">
          <span
            id="password-error"
            className="popup__input-errorMessage"
          ></span>
        </div>
      </CredentialsForm>
      <InfoToolTip
        registerFail={registerFail}
        isOpen={isOpen}
        onClose={onClose}
      />
    </>
  );
}

export default withRouter(Register);
