import React from "react";
import { withRouter } from "react-router-dom";
import CredentialsForm from "./CredentialsForm";
import Header from "./Header";

function Login(props) {
  const { values, onSubmit, onChange } = props;

  return (
    <>
      <Header headerLink={"signup"} headerLinkClass={"popup-login__register-header-link"} headerLinkText={"Sign up"} />
      <CredentialsForm
        name="login"
        titleClass="popup-login__title"
        title="Log in"
        onSubmit={onSubmit}
        buttonClass="login__submit-btn"
        buttonText= {"Log in"}
        linkContainer={"login__signup"}
        linkContainerTextClass={"login__register-text"}
        linkContainerText={"Not a member yet?"}
        linkTo={"signup"}
        linkClass={"login__register-link"}
        linkText={"Sign up here!"}
      >
        <input
          type="email"
          name="email"
          id="email"
          value={values.email}
          onChange={onChange}
          className="popup-login__input"
          placeholder="Email"
          required
          minLength="1"
          maxLength="30"
        />
        <div className="errorContainer">
          <span id="email-error" className="popup__input-errorMessage"></span>
        </div>
        <input
          type="password"
          name="password"
          id="password"
          value={values.password}
          onChange={onChange}
          className="popup-login__input"
          placeholder="Password"
          required
        />
        <div className="errorContainer">
          <span
            id="password-error"
            className="popup__input-errorMessage"
          ></span>
        </div>
      </CredentialsForm>
    </>
  );
}

export default withRouter(Login);
