import React, { useEffect } from "react";
import { Route, Switch, Redirect, useHistory } from "react-router-dom";
import Register from "./Register";
import Login from "./Login";
import ProtectedRoute from "../components/ProtectedRoute";
import Home from "./Home";
import * as auth from "../utils/auth";

// token: eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJfaWQiOiI2MWJiN2Q4YWUzNjc4MjAwMWEyOTQ2M2IiLCJpYXQiOjE2Mzk2Nzc4NzR9.u79pU_Z5Exf31gceXZHI-FroWPr2CvZXvaD02menfIo
function App() {

const [isTooltipModalOpen, setisTooltipModalOpen] = React.useState(false)
const [fail, setFail] = React.useState(false);
const [loggedIn, setLoggedIn] = React.useState(false);
const history = useHistory();
const [currentEmail, setCurrentEmail] = React.useState('')

const [registerValues, setRegisterValues] = React.useState({
  email: "",
  password: "",
});

const handleRegisterChange = (e) => {
  const { name, value } = e.target;
  setRegisterValues({ ...registerValues, [name]: value });
};

const handleRegisterSubmit = (e) => {
  e.preventDefault();
  setisTooltipModalOpen(true); 
  auth.register(registerValues)
  .then((res) => {
    setFail(false); 
    console.log({ message: 'Success! You have now been registered.' });
    history.push("/signin");
    console.log("hey")
  })
  
  .catch((err) => {
    console.log("hi")
    setFail(true);
    setisTooltipModalOpen(true);
    console.log({err, message: "Oops, something went wrong! Please try again."})
  })
};


const [loginValues, setLoginValues] = React.useState({
  email: "",
  password: "",
});

const handleLoginChange = (e) => {
  const { name, value } = e.target;
  setLoginValues({ ...loginValues, [name]: value });
};

const handleLoginSubmit = (e) => {
  e.preventDefault();
  if (!loginValues.email || !loginValues.password) {
    return;
  }
  auth.login(loginValues)
    .then((data) => {
    if (data.token) { 
      setLoginValues({
        email:"",
        password: ""
      }) 
        handleLogin(); 
        history.push(
        '/'); 
      }
    })
    .catch(err => console.log(err));
}

useEffect(() => {
  const jwt = localStorage.getItem('jwt');
  if (jwt) {
    auth.checkToken(jwt)
    .then((res) => {
      setCurrentEmail(res.data.email)
      console.log(currentEmail)
      setLoggedIn(true);
      history.push("/");
    })
    .catch((err) => {
      console.log(err)
    })
  }
})

function closeSignupPopup() {
  setisTooltipModalOpen(false);
}

function handleLogin() {
  setLoggedIn(true);
}

  return (
    <Switch>
      <Route path="/signup">
        <Register values={registerValues} onSubmit={handleRegisterSubmit} onChange={handleRegisterChange} registerFail={fail} onClose={closeSignupPopup} isOpen={isTooltipModalOpen}/>
      </Route>
      <Route path="/signin">
        <Login values={loginValues} onSubmit={handleLoginSubmit} onChange={handleLoginChange} handleLogin={handleLogin} />
      </Route>
      <ProtectedRoute path="/" loggedIn={loggedIn} component={Home}>
        <Home headerEmail={currentEmail} loggedIn={loggedIn}/>
      </ProtectedRoute>
      <Route>
        {loggedIn ? (
          <Redirect to="/" />
        ) : (
          <Redirect to="/signin" />
        )}
      </Route>
    </Switch>
  );
}

export default App;
