import PopupWithForm from "./PopupWithForm";
import React from "react";



function DelModalPopup(props) {
    const { card, isOpen,onClose} = props;


    function handleDelModalSubmit(e) {
        e.preventDefault();
        props.onCardDelete(card);
       
    }

    return(
        <PopupWithForm
          name="del-modal"
          title="Are you sure?"
          titleClass="popup__title"
          isOpen={isOpen}
          onClose={onClose}
          onSubmit={handleDelModalSubmit}
          buttonClass="del-modal__submit-btn"
          buttonText="Yes"
        >
        </PopupWithForm>
    )
}

export default DelModalPopup;