function TooltipPopup({ isOpen, onClose, ...props }) {
    return (
      <section
        className={`popup popup-${props.name} ${isOpen ? "popup_visible" : ""}`}
      >
        <div className={`popup__container popup-${props.name}__container`}>
            <button
              aria-label=""
              type="button"
              className="popup__close-btn"
              onClick={onClose}
            ></button>
            {props.children}
        </div>
      </section>
    );
  }
  export default TooltipPopup;
  