import React from "react";
import TooltipPopup from "./TooltipPopup";
import x from '../images/x.png';
import tick from '../images/tick.png';

function InfoToolTip(props) {
  const { registerFail, isOpen, onClose } = props;
  
  return (

        <TooltipPopup
          name="register-tooltip"
          isOpen={isOpen}
          onClose={onClose}
        >
          <button
            aria-label=""
            type="button"
            className="popup__close-btn"
            onClick={onClose}
          ></button>
          <img
            className="popup-register-tooltip__picture"
            src={registerFail ? x : tick}
            alt={registerFail ? "red x image" : "black tick image"}
          />
          <p className="popup-register-tooltip__text">
            {registerFail ? "Oops, something went wrong! Please try again." : "Success! You have now been registered."}
          </p>
        </TooltipPopup>
  );
}

export default InfoToolTip;
