import React from "react";
import Header from "./Header";
import Main from "./Main";
import Footer from "./Footer";
import EditProfilePopup from "./EditProfilePopup";
import EditAvatarPopup from './EditAvatarPopup';
import ImagePopup from "./ImagePopup";
import api from "../utils/api";
import { useEffect } from "react";
import {CurrentUserContext} from '../contexts/CurrentUserContext';
import AddPlacePopup from "./AddPlacePopup";
import DelModalPopup from "./DelModalPopup";
import { useHistory } from "react-router-dom";


function Home(props) {

    const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] = React.useState(false);
    const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = React.useState(false);
    const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] =
      React.useState(false);
    const [isDelModalPopupOpen, setIsDelModalPopupOpen] = React.useState(false);
    const [imageModalOpen, setImageModalOpen] = React.useState(false);
    const [selectedCard, setSelectedCard] = React.useState({});
    const [currentUser, setCurrentUser] = React.useState({});
    const [cardToDelete, setCardToDelete] = React.useState({});
  
    function handleEditAvatarClick() {
      setIsEditAvatarPopupOpen(true);
    }
  
    function handleEditProfileClick() {
      setIsEditProfilePopupOpen(true);
    }
  
    function handleAddPlaceClick() {
      setIsAddPlacePopupOpen(true);
    }
  
    function handleDelModalClick(card) {
      setCardToDelete(card);
      setIsDelModalPopupOpen(true);
    }

  
    function closeAllPopups() {
      setIsEditAvatarPopupOpen(false);
      setIsEditProfilePopupOpen(false);
      setIsAddPlacePopupOpen(false);
      setIsDelModalPopupOpen(false);
      setImageModalOpen(false);
    }
  
    const cardClick = (newSelectedCard) => {
      setSelectedCard(newSelectedCard);
  
      setImageModalOpen(true);
    };
  
    useEffect(() => {
      api
        .getUserInfo()
        .then((res) => {
          setCurrentUser(res);
        })
        .catch((err) => {
          console.log(err);
        });
    }, []);
  
    function handleUpdateUser(data) {
      api.setUserInfo(data)
      .then((res) => {
        setCurrentUser(res);
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      });
    }
  
    function handleUpdateAvatar(data) {
      api.setUserAvatar(data)
      .then((res) => {
        setCurrentUser(res);
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      });
    }
  
    const [cards, setCards] = React.useState([]);
  
    useEffect(() => {
      api
        .getInitialCards()
        .then((res) => {
          setCards(res);
        })
  
        .catch((err) => {
          console.log(err);
        });
    }, []);
  
    function handleCardLike(card) {
      const isLiked = card.likes.some(i => i._id === currentUser._id);
  
      api.likeCard(card._id, !isLiked).then((newCard) => {
          setCards((state) => state.map((c) => c._id === card._id ? newCard : c));
      })
      .catch((err) => {
        console.log(err);
      });
  } 
  
  function handleCardDislike(card) {
    const isLiked = card.likes.some(i => i._id === currentUser._id);
  
    api.dislikeCard(card._id, isLiked).then((newCard) => {
        setCards((state) => state.map((c) => c._id === card._id ? newCard : c));
    })
    .catch((err) => {
      console.log(err);
    });
  }
  
  function handleCardDelete(card) {
    const isOwner = card.owner._id === currentUser._id;
  
    api.deleteCard(card._id, isOwner)
    .then((newCard) => {
      setCards((state) => state.filter((c) => c._id !== card._id));
      closeAllPopups();
  })
  .catch((err) => {
    console.log(err);
  });
  }
  
  function handleAddCard(data) {
    api.createCard(data)
    .then((newCard) => {
      setCards([newCard, ...cards])
      closeAllPopups();
    })
    .catch((err) => {
      console.log(err);
    });
  }

  const history = useHistory();
  function signOut() {
      localStorage.removeItem('jwt');
      history.push('/signin');
    }

return (
    <div className="page">
      <CurrentUserContext.Provider value={currentUser}>
        <Header headerEmail={props.children.props.headerEmail} headerLink={"signin"} headerLinkClass={"popup-login__register-header-link"} onClick={signOut} headerLinkText={"Log out"} />
        <Main
          onEditAvatarClick={handleEditAvatarClick}
          onEditProfileClick={handleEditProfileClick}
          onAddPlaceClick={handleAddPlaceClick}
          onDelModalClick={handleDelModalClick}
          cards={cards}
          onCardLike={handleCardLike}
          onCardDislike={handleCardDislike}
          onCardDelete={() => handleCardDelete()}
          cardClick={cardClick}
        />
        

        <EditAvatarPopup
          isOpen={isEditAvatarPopupOpen} 
          onClose={closeAllPopups}
          onUpdateAvatar={handleUpdateAvatar} />

        <EditProfilePopup
          isOpen={isEditProfilePopupOpen}
          onClose={closeAllPopups} 
          onUpdateUser={handleUpdateUser} />

         <AddPlacePopup
          isOpen={isAddPlacePopupOpen}
          onClose={closeAllPopups} 
          onCardAdd={handleAddCard} />

          <DelModalPopup 
          isOpen={isDelModalPopupOpen}
          onClose={closeAllPopups}
          onCardDelete={() => handleCardDelete(cardToDelete)}
          card={cardToDelete}
          />

        <ImagePopup
          card={selectedCard}
          cardClickHandler={cardClick}
          isOpen={imageModalOpen}
          onClose={closeAllPopups}
        ></ImagePopup>

        <Footer />
      </CurrentUserContext.Provider>
    </div>
  );
}

export default Home;