function PopupWithForm({ onSubmit, isOpen, onClose, ...props }) {
  return (
    <section
      className={`popup popup-${props.name} ${isOpen ? "popup_visible" : ""}`}
    >
      <div className={`popup__container popup-${props.name}__container`}>
        <form
          className={`popup__form popup-${props.name}__form`}
          name={`form-${props.name}`}
          id={`form-${props.name}`}
          onSubmit={onSubmit}
        >
          <button
            aria-label=""
            type="button"
            className="popup__close-btn"
            onClick={onClose}
          ></button>

          <h2 className={props.titleClass}>{props.title}</h2>
          {props.children}
          <button
            aria-label=""
            id={props.name}
            type="submit"
            className= "popup__submit-btn"
            
          >
              {props.buttonText}
            </button>
        </form>
      </div>
    </section>
  );
}
export default PopupWithForm;
